.body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-size: 14px;
}

.general-container {
  min-width: 320px;
  width: 95%;
  @media (max-width: 900px) {
    width: 100%;
  }
  min-height: 728px;
  border-radius: 40px;
  //overflow: hidden;
  display: flex;
  flex-direction: column;
  background: radial-gradient(50% 112.5% at 50% 50%, #FFEFFB 0%, #FAF7FF 50.52%, #E8E8FF 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
  , linear-gradient(113.96deg, #FFE9FB 0%, #FAF7FF 50.52%, #E0DFFF 100%), #F4F3F8;
}

.white-part-container {
  min-width: 260px;
  // flex-basis: 260px;
  background-color: #FCFAFF;
}

.purple-accent {
  font-weight: 700;
  color: #936DFF;
}

.error-accent {
  color: #FFA7B7;
}

.link {
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.02em;
  text-decoration-line: underline;
  color: #936dff;
  transition: 0.3s;

  &:hover {
    color: #d96dff;
  }
}

.small-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.02em;
  text-decoration-line: underline;
  color: #adadd9;
  transition: 0.3s;

  &:hover {
    color: #d96dff;
  }
}

.copy-link-button {
  cursor: pointer;

  & path {
    transition: 0.3s;

    &:hover {
      fill: #d96dff;
    }
  }

}

.search-field {
  &--small {
    border-radius: 8px;
    border-color: rgba(212, 212, 244, 0.1);
    box-sizing: border-box;
    flex-basis: 200px;
    flex-grow: 1;
    height: 25px;
    position: relative;
    background-color: rgba(212, 212, 244, 0.1);
    padding: 5px 10px 3px 10px;

    &:focus {
      border-color: rgba(212, 212, 244, 0.5);
      outline: none;
    }

    &::placeholder {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: #D4D4F4;
    }

    &__search-button {
      position: relative;
      background-image: url(../images/icons/icon_search.svg);
      width: 14px;
      height: 14px;
      display: inline-block;
      right: 24px;
    }

  }

  &--big {
    border-radius: 8px;
    border-color: rgba(212, 212, 244, 0.1);
    box-sizing: border-box;
    flex-basis: 200px;
    flex-grow: 1;
    height: 44px;
    position: relative;
    background-color: rgba(212, 212, 244, 0.1);
    padding: 10px 20px;

    &:focus {
      border-color: rgba(212, 212, 244, 0.5);
      outline: none;
    }

    &::placeholder {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: #D4D4F4;
    }

    &__search-button {
      position: relative;
      background-image: url(../images/icons/icon_search.svg);
      width: 14px;
      height: 14px;
      display: inline-block;
      right: 30px;
    }
  }
}

.filter-icon {

  &:hover {
    & path {
      fill: rgba(217, 109, 255, 1);
    }
  }

  &--activated {
    & path {
      fill: rgba(147, 109, 255, 1);
    }
  }
}
