.header {

  display: flex;
  flex-direction: row;
  height: 136px;

  font-weight: 400;
  letter-spacing: 0.02em;
  color: #4e4160;


  &__white-part-container {
    box-sizing: border-box;
    //padding: 48px 56px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }

  .logo-link {
    display: block;

    img {
      display: block;
    }
  }

  &__transparent-part-container {
    height: 136px;
    box-sizing: border-box;
    //padding: 48px 0 0 44px;
  }

  &__server-name {
    margin: 48px 0 0 44px;
    font-size: 28px;
    line-height: 100%;
  }

  &__server-ip {
    margin: 8px 0 0 44px;
    font-size: 16px;
    line-height: 100%;
  }
}